.profile-container {
  max-width: 900px;
  /* margin: 0 auto; */
  /* margin-top: 150px; */
  /* padding: 40px; */
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.profile-form {
  display: flex;
  flex-direction: column;
}


.input-field,
.select-field {
  width: 100%;
  /* padding: 10px; */
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-top: 5px;
  margin-top: 20px;
}


.save-button {
  background-color: #0073b1;
  margin-top: 40px;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 10px;
}


.save-button:hover {
  background-color: #005d8e;
}


/* .profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
} */

.profile-form {
  width: 80%; 
}

.profile-image-container {
  position: relative;
  margin-bottom: 20px; 
  background-color: #fff;
}

.profile-image {
  display: block;
  border-color: #005d8e;
  width: 100px; /* Adjust image size */
  height: 100px; /* Adjust image size */
  border-radius: 50%;
  object-fit: cover; /* Ensure image fills circular area */
  margin-top: 20px;
  margin-bottom: 0px;
}

.profile-image-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
