/* Profile.css */

.log-container {
    max-width: 250px;
    margin: 0 auto;
    margin-top: 150px;
    padding: 40px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
    color:#005d8e;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  h1{
    margin-top: 30px;
    margin-bottom: 25px;
    font-size:25px;
  }
  
  .profile-form {
    display: flex;
    flex-direction: column;
  }
  
  .label-container {
    margin-bottom: 20px;
  }
  
  .input-field,
  .select-field,
  .upload-field {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-top: 5px;
    margin-top: 20px;
  }
  .save-button {
    background-color: #0073b1;
    margin-top: 40px;
  }
  .upload-button,
  .save-button,
  .yes-no-button {
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 10px;
  }
  
  .upload-button:hover,
  .save-button:hover,
  .yes-no-button:hover {
    background-color: #005d8e;
  }
  .label-text,
  .checkbox-label,
  .upload-label {
    margin-top: 40px;
    color: #005d8e;
  }
  .checkbox-container {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  .checkbox-label {
    font-size: 16px;
    margin-left: 10px;
  }
  
  .upload-label {
    font-size: 18px;
    margin-top: 40px;
    margin-bottom: 0px;
  }
  
  .upload-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .upload-field label {
    display: inline-block;
    background-color: #0073b1;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 5px;
    text-align: center;
  }
  
  .upload-field input {
    display: none;
  }
  